.search {
  padding-top: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.search .search-input {
  position: absolute;
}
.search .search-input input {
  width: 700px;
  font-size: 20px;
  font-weight: bold;
  padding: 10px;
  border: 2px solid #000;
  border-radius: 10px;
}
.search .search-input :focus-visible {
  outline: 1px solid #80B918;
}
.search .search-icon {
  position: relative;
  left: 323px;
  border: 2px solid #000;
  padding: 5px 10px;
  border-radius: 10px;
  background-color: #80B918;
  cursor: pointer;
}

@media (max-width: 992px) {
  .search .search-input input {
    width: 600px;
  }
  .search .search-icon {
    left: 274px;
  }
}
@media (max-width: 768px) {
  .search .search-input input {
    width: 500px;
  }
  .search .search-icon {
    left: 224px;
  }
}
@media (max-width: 576px) {
  .search .search-input input {
    width: 300px;
  }
  .search .search-icon {
    left: 124px;
  }
}